<template>
  <b-nav-item-dropdown
    class=""
    menu-class="dropdown-menu-media"
    right
  >
    <template #button-content>
      <feather-icon
        :badge="total"
        badge-classes="bg-danger"
        class="text-body"
        icon="BellIcon"
        size="21"
        stroke="white"
      />
    </template>

    <!-- Header -->
    <li class="dropdown-menu-header">
      <div class="dropdown-header d-flex">
        <h4 class="notification-title mb-0 mr-auto">
          {{ $t('Notifications') }}
        </h4>
        <b-badge
          pill
          variant="light-primary"
        >
          {{ notifications.length }}
        </b-badge>
      </div>
    </li>

    <!-- Notifications -->

    <!-- Account Notification -->
    <div
      style="min-height: 10px;
             max-height: 40vh;
             overflow: auto;
             width: 30vw;
             "
    >
      <transition-group
        name="list"
        mode="out-in"
      >
        <div
          v-for="(notification) in notifications"
          :key="notification.id"
        >

          <div class="m-0 mt-1 p-0 pl-1 pr-1">
            <!-- <template #aside> -->
            <!-- <b-avatar
            size="32"
            :src="notification.avatar"
            :text="notification.avatar"
            :variant="notification.type"
          /> -->
            <!-- </template> -->
            <p
              class="d-flex justify-content-between"
              style="font-size: 1rem"
            >
              <span class="font-weight-bolder">
                {{ notification.title }} <small>{{ DateChanged(notification.created_at) }}</small>
              </span>
              <feather-icon
                class="saj-scale-1 pointer"
                icon="XIcon"
                size="20"
                @click="getRead(notification.id)"
              />
            </p>
            <p>

              <small class="notification-text">{{ notification.message }}</small></p>
            <a
              v-if="notification.file !== null"
              class="m-0"
              :href="notification.file"
              target="_blank"
              style="
                     text-decoration: underline;
                     color: rgb(85, 85, 255);
                     font-style: italic;
                     font-size: 0.8rem;
                    "
            >
              {{ 'Click here to download' }}
            </a>

            <!-- <hr style="border-bottom: 2px solid #FF0000"> -->
            <hr>
          </div>
        </div>
      </transition-group>
    </div>

    <!-- System Notification Toggler -->
    <!-- <div class="media d-flex align-items-center">
        <h6 class="font-weight-bolder mr-auto mb-0">
          System Notifications
        </h6>
        <b-form-checkbox
          :checked="true"
          switch
        />
      </div> -->

    <!-- System Notifications -->
    <!-- <b-link
        v-for="notification in systemNotifications"
        :key="notification.subtitle"
      >
        <b-media>
          <template #aside>
            <b-avatar
              size="32"
              :variant="notification.type"
            >
              <feather-icon :icon="notification.icon" />
