          </template>
          <p class="media-heading">
            <span class="font-weight-bolder">
              {{ notification.title }}
            </span>
          </p>
          <small class="notification-text">{{ notification.subtitle }}</small>
        </b-media>
      </b-link> -->

    <!-- Cart Footer -->
    <!-- <li class="dropdown-menu-footer"><b-button
      v-ripple.400="'rgba(255, 255, 255, 0.15)'"
      variant="primary"
      block
    >Read all notifications</b-button>
    </li> -->
  </b-nav-item-dropdown>
</template>

<script>
import {
  BNavItemDropdown, BBadge,
  // BMedia,
  // BLink,
  // BAvatar,
  // BButton,
  // BFormCheckbox,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import store from '@/store/index'
import axios from "axios"
import moment from "moment"

export default {
  components: {
    BNavItemDropdown,
    BBadge,
    // BMedia,
    // BLink,
    // BAvatar,
    // BButton,
    // BFormCheckbox,
  },
  directives: {
    Ripple,
  },
  data(){
    return {
      notifications: [
        // {
        //   title: "",
        //   message: "",
        // },
      ],
      read: 0,
      total: 0,
    }
  },
  mounted(){
    // // setInterval(this.getNotification(), 3000)

    window.setInterval(() => {
      if (store.getters.sajRanhillToken !== null){
        this.getNotification()
        // console.log(store.getters.sajRanhillToken)
      }
    }, 10000)
  },
  created() {

  },
  methods: {
    getNotification(){
      // console.log('kalau ada', store.getters.sajRanhillToken)
      // console.log('noti')
      axios.get(`${this.$baseUrl}/notification/get_notification_all`).then(response => {
        // if (response.data.success === true){
        this.notifications = response.data.data
        this.total = this.notifications.length
        // console.log(this.notifications)
        // console.log(store.getters.sajRanhillToken)
      })
    },
    getRead(idd) {
      axios.post(`${this.$baseUrl}/notification/update_notification`, { id: idd, is_read: '1' }).then(() => {
        this.getNotification()
      })
    },

    DateChanged(date) {
      const newDay = moment(date).format("D-MMM-yyyy")
      return newDay
    },
  },
  setup() {
    /* eslint-disable global-require */
    // const notifications = [
    //   {
    //     title: 'Congratulation Sam ',
    //     avatar: require('@/assets/images/avatars/6-small.png'),
    //     subtitle: 'Won the monthly best seller badge',
    //     type: 'light-success',
    //   },
    //   {
    //     title: 'New message received',
    //     avatar: require('@/assets/images/avatars/9-small.png'),
    //     subtitle: 'You have 10 unread messages',
    //     type: 'light-info',
    //   },
    //   {
    //     title: 'Revised Order ',
    //     avatar: 'MD',
    //     subtitle: 'MD Inc. order updated',
    //     type: 'light-danger',
    //   },
    // ]
    /* eslint-disable global-require */

    // const systemNotifications = [
    //   {
    //     title: 'Server down',
    //     subtitle: 'USA Server is down due to hight CPU usage',
    //     type: 'light-danger',
    //     icon: 'XIcon',
    //   },
    //   {
    //     title: 'Sales report generated',
    //     subtitle: 'Last month sales report generated',
    //     type: 'light-success',
    //     icon: 'CheckIcon',
    //   },
    //   {
    //     title: 'High memory usage',
    //     subtitle: 'BLR Server using high memory',
    //     type: 'light-warning',
    //     icon: 'AlertTriangleIcon',
    //   },
    // ]

    const perfectScrollbarSettings = {
      maxScrollbarLength: 60,
      wheelPropagation: false,
    }

    return {
      // notifications,
      // systemNotifications,
      perfectScrollbarSettings,
    }
  },
}
</script>

