<template>

  <div
    class="p-1 modalScroll "
    style="width: auto; height: 55vh;"
  >
    <div
      class="mb-1 saj-title d-flex justify-content-center mt-2"
      style=""
    >
      {{ $t ("Update Password") }}
      <!-- <br>email ni:{{ email }} -->
    </div>
    <validation-observer ref="validatePassword">
      <div class="d-flex justify-content-center">

        <div class="form-group col-lg-8 col-sm-8 col-md-8 mb-2 mt-3 ">
          <b-form-group
            class="saj-text"
            :label="$t('Old Password')"
          >
            <validation-provider
              #default="{ errors }"
              :name="$t('Old Password')"
              rules="required"
            >
              <input
                id="new_password"
                v-model="newPassword"
                :type="[showPassword ? 'text' : 'password']"
                class="form-control form-control-lg"
                :placeholder="$t('Old Password')"
              >
              <i
                class="fa field-icon"
                :class="[showPassword ? 'fa-eye' : 'fa-eye-slash']"
                aria-hidden="true"
                @click="showPassword = !showPassword"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </div>
      </div>

      <div class="d-flex justify-content-center">
        <div class="form-group col-lg-8 col-sm-8 col-md-8 mb-3 ">
          <b-form-group
            class="saj-text"
            :label="$t('New Password')"
          >
            <validation-provider
              #default="{ errors }"
              :name="$t('New Password')"
              rules="required"
            >
              <input
                id="confirm_password"
                v-model="retypePassword"
                :type="[confirmPassword ? 'text' : 'password']"
                class="form-control form-control-lg"
                :placeholder="$t('Re-type New Password')"
              >
              <i
                class="fa field-icon"
                :class="[confirmPassword ? 'fa-eye' : 'fa-eye-slash']"
                aria-hidden="true"
                @click="confirmPassword = !confirmPassword"
              />
              <!-- <div
          id="message3"
          style="color: red; font-size: 11px;"
        /> -->
              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>
          </b-form-group>
        </div>
      </div>

      <div
        class="d-flex justify-content-end"
        style=""
      >
        <button
          class="btn btn-close m-1 saj-button"
          aria-label="Close modal"
          style="
                  color: white;
                  background: #ff0000;
                "
          @click="close()"
        >
          {{ $t('Cancel') }}
        </button>
        <button
          class="btn m-1 saj-button"
          aria-label="submit modal"
          style="
                  color: white;
                  background: #0b103c !important;
                  border-color: #0b103c !important;
                "
          @click="changePassword()"
        >
          {{ $t ("Update Password") }}
        </button>
      </div>
    </validation-observer></div>

</template>
